html {
  font-size: 62.5%;
  min-width: 320px;
  @media screen and (max-width: 1550px) {
    font-size: 60%;
  }
  @media screen and (max-width: 1400px) {
    font-size: 58%;
  }
  @media screen and (max-width: 1300px) {
    font-size: 54%;
  }
  @media screen and (max-width: 1200px) {
    font-size: 50%;
  }
}
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
a {
  text-decoration: none;
}

body {
  font-family: var(--font-family);
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 1;
  background: #000;
}
body::after {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  width: 1px;
  height: 100vh;
  background: blue;
  display: none;
}
input,
button,
textarea {
  font-family: var(--font-family);
}
button {
  cursor: pointer;
}
img {
  max-width: 100%;
  height: auto;
}

ul {
  list-style: none;
}

p {
  margin: 0;
  padding: 0;
}
.title {
  font-size: 3rem;
  font-weight: 700;
  line-height: normal;
  color: #fff;
}
.text {
  text-align: justify;
  font-size: 1.6rem;
  line-height: 1.50;
  color: #fff;
}

.hidden {
  display: none;
}

.container {
  margin: 0 auto;
  max-width: 1860px;
  padding: 0 15px;
}

input[type="submit"]:disabled {
  cursor: not-allowed !important;
  opacity: 0.6 !important;
}
